import { template } from "@ember/template-compiler";
import Create from './create';
export default template(`
  <section class="todoapp">
    <header class="header">
      <h1>todos</h1>
      <Create/>
    </header>

    {{!-- {{yield}}
      {{#let (service "repo") as |repo|}}
        {{#if (hasTodos repo.all)}}
          <Footer />
        {{/if}}
      {{/let}} --}}
  </section>
`, {
    eval () {
        return eval(arguments[0]);
    }
});
